<template>
  <div class="pt-2">
    <loading :active.sync="isLoading"></loading>
    <h2></h2>
    <div class="text-right">
      <button type="button" class="btn btn-dark" @click="openModel('add')" data-toggle="modal" data-target="#exampleModal"></button>
    </div>
    <table class="table table-striped">
      <thead>
    <tr>
      <th scope="col"></th>
      <th scope="col"></th>
      <th scope="col"></th>
      <th scope="col"></th>
      <th scope="col"></th>
      <th scope="col"></th>
      <th scope="col"></th>
    </tr>
  </thead>
        <tbody>
          <tr v-for="item in products" :key="item.id">
        <th scope="row"><button type="button" class="btn btn-outline-danger btn-sm" @click="openModel('remove',item)"></button></th>
        <td><img :src="item.imageUrl" class="img-fluid" style="height:100px" alt=""></td>
        <td>{{item.category}}</td>
        <td>{{item.title}}</td>
        <td>{{item.origin_price | total}}</td>
        <td>{{item.price | total}}</td>
        <td>
          <button type="button" class="btn btn-outline-dark btn-sm" @click="openModel('edit',item)"></button>
          <!-- <router-link :to="`/product/${item.id}`"></router-link> -->
        </td>
        </tr>
        </tbody>
    </table>
    <Pagination :pages="pagination" @emitPages="getData" />
    <!-- Modal -->
    <div
      id="productModal"
      class="modal fade"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div
        class="modal-dialog modal-xl"
        role="document"
      >
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5
              id="exampleModalLabel"
              class="modal-title"
            >
              <span v-if="isNew"></span>
              <span v-else> {{ tempProduct.title }} </span>
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div
                  v-for="i in 5"
                  :key="i + 'img'"
                  class="form-group"
                >
                  <label :for="'img' + i"></label>
                  <input
                    :id="'img' + i"
                    v-model="tempProduct.imageUrl[i - 1]"
                    type="text"
                    class="form-control"
                    placeholder=""
                  >
                </div>
                <div class="form-group">
                  <label for="customFile">
                     
                    <i
                      class="fas fa-spinner fa-spin" v-if="status.fileUploading"
                    />
                  </label>
                  <input
                    id="customFile"
                    ref="file"
                    type="file"
                    class="form-control"
                    @change="uploadFile"
                  >
                </div>
                <img
                  class="img-fluid"
                  :src="tempProduct.imageUrl[0]"
                  alt
                >
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    id="title"
                    v-model="tempProduct.title"
                    type="text"
                    class="form-control"
                    placeholder=""
                    required
                  >
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input
                      id="category"
                      v-model="tempProduct.category"
                      type="text"
                      class="form-control"
                      placeholder=""
                      required
                    >
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      id="unit"
                      v-model="tempProduct.unit"
                      type="text"
                      class="form-control"
                      placeholder=""
                    >
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input
                      id="origin_price"
                      v-model="tempProduct.origin_price"
                      type="number"
                      class="form-control"
                      placeholder=""
                    >
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      id="price"
                      v-model="tempProduct.price"
                      type="number"
                      class="form-control"
                      placeholder=""
                    >
                  </div>
                </div>
                <hr>

                <div class="form-group">
                  <label for="description"></label>
                  <textarea
                    id="description"
                    v-model="tempProduct.description"
                    type="text"
                    class="form-control"
                    placeholder=""
                    required
                  />
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <vue-editor
                    id="content"
                    v-model="tempProduct.content"
                  />
                  <!-- <textarea name="content" id="content" class="form-control" v-model="tempProduct.content"></textarea> -->
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input
                      id="is_enabled"
                      v-model="tempProduct.enabled"
                      class="form-check-input"
                      type="checkbox"
                    >
                    <label
                      class="form-check-label"
                      for="is_enabled"
                    ></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-outline-secondary"
              data-dismiss="modal"
            >
              
            </button>
            <button
              type="button"
              class="btn btn-primary"
              @click="updateProduct"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- Modal -->
<div class="modal fade" id="delProductModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog" role="document">
    <div class="modal-content">
      <div class="modal-header bg-danger text-white">
        <h5 class="modal-title" id="exampleModalLabel"></h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
          
            <strong class="text-danger">{{ tempProduct.title }}</strong> ()
      </div>
      <div class="modal-footer">
            <button
              type="button"
              class="btn btn-outline-secondary"
              data-dismiss="modal"
            >
              
            </button>
            <button
              type="button"
              class="btn btn-danger"
              @click="delProduct"
            >
              
            </button>
      </div>
    </div>
  </div>
</div>
</div>
</template>

<script>
import $ from 'jquery'
import Pagination from '@/components/Pagination.vue'
import { VueEditor } from 'vue2-editor/dist/vue2-editor.core'

export default {
  name: 'Products',
  components: {
    Pagination,
    VueEditor
  },
  data () {
    return {
      products: {},
      pagination: {},
      tempProduct: {
        imageUrl: []
      },
      isNew: false,
      productName: '',
      isLoading: false,
      status: {
        fileUploading: false
      }
    }
  },
  props: ['tokens'],
  created () {
    this.getData()
  },
  methods: {
    // 
    getData (page = 1, paged = 10) {
      this.isLoading = true
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/ec/products?page=${page}&paged=${paged}`
      this.$http.get(url)
        .then((response) => {
          this.isLoading = false
          this.products = response.data.data
          this.pagination = response.data.meta.pagination
        }).catch((err) => {
          console.log(err)
        })
    },
    // 
    openModel (isNew, item) {
      switch (isNew) {
        case 'add':
          this.isNew = true
          this.tempProduct = {
            imageUrl: []
          }
          this.productName = ''
          this.isNew = true
          $('#productModal').modal('show')
          break
        case 'edit': {
          this.isNew = false
          this.isLoading = true
          const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/ec/product/${item.id}`
          this.$http.get(url).then((response) => {
            this.tempProduct = response.data.data
            this.isLoading = false
            this.productName = ''
            console.log(this.tempProduct)
            $('#productModal').modal('show')
            this.loadingBtn = ''
          })
          break
        }
        case 'remove':
          this.tempProduct = { ...item }
          $('#delProductModal').modal('show')
          break
      }
    },
    // 
    uploadFile () {
      const uploadedFile = this.$refs.file.files[0]
      const formData = new FormData()
      formData.append('file', uploadedFile)
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/storage`
      this.status.fileUploading = true
      this.$http.post(url, formData, {
        'Content-Type': 'multipart/form-data'
      }).then((response) => {
        this.status.fileUploading = false
        if (response.status === 200) {
          this.tempProduct.imageUrl.push(response.data.data.path)
        }
      }).catch(() => {
        this.$bus.$emit('message:push',
          `(   |||)
           2MB`,
          'danger')

        this.status.fileUploading = false
      })
    },
    // 
    updateProduct () {
      this.isLoading = true
      // 
      let url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/ec/product`
      let httpUrl = 'post'
      // api
      if (!this.isNew) {
        url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/ec/product/${this.tempProduct.id}`

        httpUrl = 'patch'
      }
      this.$http[httpUrl](url, this.tempProduct).then((response) => {
        this.isLoading = false
        this.$bus.$emit('message:push', '~',
          'success')

        this.getData()
        $('#productModal').modal('hide')
      }).catch((error) => {
        this.isLoading = false
        const errorData = error.response.data
        this.$bus.$emit('message:push',
          `!!!${errorData}`,
          'danger')
      })
    },
    delProduct () {
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_UUID}/admin/ec/product/${this.tempProduct.id}`
      this.isLoading = true
      this.$http.delete(url).then(() => {
        this.isLoading = false
        this.$bus.$emit('message:push', '~',
          'success')
        this.getData()
        $('#delProductModal').modal('hide')
      })
    }
  }
}
</script>

<style lang="css">
@import '~vue2-editor/dist/vue2-editor.css';
/* Import the Quill styles you want */
@import '~quill/dist/quill.core.css';
@import '~quill/dist/quill.bubble.css';
@import '~quill/dist/quill.snow.css';
</style>
